.newslist_item {
    border: 1px solid #f2f2f2;
    background: #fff;
    margin-top: 0px;
    padding: 15px 10px 0px 10px;
}

.newslist_item h2 {
    font-size: 14px;
    line-height: 21px;
    margin: 15px 0px;
    color: #525252;
}

.newslist_item a {
    text-decoration: none;
}

.newsList_wrapper {
    box-sizing: border-box;
    opacity: 0;
    transform: translateX(-100%);
    transition: all .5s ease-in;
}

.newsList_wrapper_enter {
    opacity: 1;
    transform: translate(0%);
}

.flex_wrapper {
    display: flex;
    border-bottom: 2px solid #d5d5d5;
    background: #fff;
}

.left {
    background-size: cover !important;

}

.left div {
    width: 90px;
    height: 90px;
    background: 40px !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;

}

.right {
    flex-grow: 1;
    padding: 10px;
}

.flex_wrapper h2 {
    font-size: 12px;
    color: #525252;
}