.featured_item {
    position: relative;
}

.featured_item a {
    position: absolute;
    width: 100%;
    bottom: 0px;
    text-decoration: none;
    right: 0px;
}

.featured_image {
    height: 665px;
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: top center;
}

.featured_caption {
    color: #fff;
    top: 0px;
    width: 100%;
    padding: 20px;
    font-weight: 300;
    font-size: 28px;
    box-sizing: border-box;
}

@media (max-width: 991.98px) {
    .featured_image {
        height: 500px;
    }
}

@media (max-width: 575.98px) {
    .featured_image {
        height: 330px;
    }
}